import React from 'react'
import Link from 'gatsby-link'
import courserap from '../images/courserap.png'



const CourseraPage = () => (

  <div>

    <div className="Main"> 
    <div className="pMain"> 
    
      <div className="pGroupOne">
        <div className="ptitle">
          <h1>Designing for Social eLearning: A Coursera App Redesign Case Study</h1><br></br>
          <p>Exploring ways to create (re)designs capable of sparking the cognition and emotions to facilitate learning.</p>
        </div>
        <img src={courserap}></img>
      </div>

      <div className="row pSummary">
        <div className="col-3">
          <h6>Tools</h6>
          <p>Sketch, Principle</p><br/>
          <h6>Duration</h6>
          <p>1 week</p><br/>
          <h6>Team</h6>
          <p>Amy Lin<br/>Hannah Kang</p><br/>
        </div>
        <div className="col-9">
          <p>
            <strong>As a self-taught designer, I am a huge fan of eLearning tools.</strong>
          </p><br/>
          <p>
            Coursera is one of the tools that has allowed individuals across the world to learn from professors at some of the nation’s top institutions. Regardless of location or social background, anyone can expand their knowledge to transform their life or progress their career. But as with any class—and with massive open online courses (MOOCs) like Coursera, in particular—completing a course does not come without significant obstacles.
          </p><br/><br/>
        </div>
      </div>

<hr/>

      <div className="pGroupLight">
        <h2>Overview</h2><br/>
        <p>
          How can Coursera better help learners achieve their goals? And, how can I create (re)designs capable of sparking the cognition and emotions to facilitate learning?
        </p><br/>
        <p>
          This is a huge question, and Coursera is a huge platform. So, I approached this project through three separate lenses while conducting research at every step of the way.
        </p><br/>
        
        <p><a href="#part1"><span className="underlinelinks">1. The learning experience</span></a></p>
        <p><span className="underlinelinks">2. The visual interface</span></p>
      </div>
<hr/>
      <div className="pGroupLight" id="part1">

        <h3>1. The Learning Experience</h3><br/>
        <p>MOOCs are notorious for their low retention rates. Despite the sheer number of people that register for courses on MOOCs, a very small proportion of those students actually proceed to complete the course.</p><br/>

        <p>Coursera’s users seem to lie somewhere between one of the following groups:</p>
        <ul>
        <li><span className="highlight">The dedicated student</span> has a strong intrinsic or external motivation to succeed and are aiming for certification. They actively participate in the course content by watching lectures and contributing to forum discussions.</li>
        <li><span className="highlight">The casual learner</span> starts the course fueled by curiosity (“Hey, that seems pretty interesting” or “My New Years’ Resolution is to learn”), but the initial excitement wears off and watching lecture videos eventually becomes a chore.</li>
        <li><span className="highlight">The browsing passerby</span> never really intended to complete the course. They just like to think that they will.</li>
        </ul><br/>
        <p>Coursera appears to mainly target the first category, and has taken steps to help those in the second category succeed.</p><br/>
        <p>I talked to a number of Coursera course alumni and dropouts to learn more about users’ experiences with taking courses on this platform.</p>

      </div>


    </div>
  </div>
  </div>

)

export default CourseraPage



